
import { Component, Vue } from 'vue-property-decorator'
import { QrcodeStream } from 'vue-qrcode-reader'
import routePaths from '@/constants/routePaths'
import errorMessages from '@/constants/errorMessages'

@Component({
  components: {
    QrcodeStream
  }
})
export default class QrCodeScanner extends Vue {
  async handleError(promise) {
    try {
      await promise
    } catch (err: any) {
      if (errorMessages[err.name]) {
        this.$emit('error', errorMessages[err.name])
      } else {
        this.$emit('error', err.message)
      }
    } finally {
      this.$emit('loading', false)
    }
  }

  async handleDetect(detectedCodes) {
    try {
      const result = await detectedCodes
      this.$router.push({
        path: routePaths.result,
        query: {
          r: result.content
        }
      })
    } catch (err: any) {
      this.$emit('error', err.message)
    }
  }

  paintCenterText(detectedCodes, ctx) {
    for (const detectedCode of detectedCodes) {
      const { boundingBox, rawValue } = detectedCode

      const centerX = boundingBox.x + boundingBox.width / 2
      const centerY = boundingBox.y + boundingBox.height / 2

      const fontSize = Math.max(12, (50 * boundingBox.width) / ctx.canvas.width)

      ctx.font = `bold ${fontSize}px sans-serif`
      ctx.textAlign = 'center'

      ctx.lineWidth = 3
      ctx.strokeStyle = '#35495e'
      ctx.strokeText(detectedCode.rawValue, centerX, centerY)

      ctx.fillStyle = '#5cb984'
      ctx.fillText(rawValue, centerX, centerY)
    }
  }
}
